

.goodsManageCont{
  margin-top: 16px;
  min-height:calc(100vh - 80px);
  .ant-card-body{
    .goodsManagementCont{
      min-height:calc(100vh - 180px);
      height: auto;
      overflow: hidden;
      .goodsTable{
        margin-top:  20px;
        .ant-table-column-has-sorters>.ant-table-column-sorters{
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .showImg{
          width:50px;
          height:50px;
          margin-right:10px;
        }
      }
    }  

    // 分页样式
    .pagCont{
      display: flex;
      justify-content:space-between;
      align-items: center;
      margin: 20px 0;
    }
  }        
}



