.ant-layout-has-sider .storeClassName {
    font-size: 18px;
    font-weight: bold;
    color: #000;
    padding: 0 16px;
}

.ant-layout-has-sider .logoNameCont {
    width: 220px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 16px;
    margin: 5px 0;
}

.ant-layout-has-sider .logoNameCont .logo {
    height: 30px;
    width: 30px;
    background: url('./img/logo.png') no-repeat center center;
    background-size: 100%;
    border-radius: 6px;
    margin: 10px 10px 10px 0;
}

/* // Header */
.ant-layout-header{
    padding: 0;
    width: 100%;
   .logoNameCont{
        margin: 0;
   }
   .headMenu{
       line-height: 64px;
       float: right;
       margin-right: 20px;
   }
}
.ant-menu-submenu-horizontal .ant-menu-vertical .logout{
    padding: 0;
    text-align: center;
    padding-left: 0;
}
.logout span{
    display: inline-block;
    width: 60%;
    height: 100%;
}

