.ossContent{
    .clearfix{
        display: flex;
        flex-wrap: wrap;
        .ant-upload-select-picture-card i {
            font-size: 32px;
            color: #999;
          }
          
          .ant-upload-select-picture-card .ant-upload-text {
            margin-top: 8px;
            color: #666;
          }
        .uploadImgListCont{
            position: relative;
            max-width:102px;
            max-height:102px;
            margin:0 20px 20px 0;
            .ullImg{
                max-width:102px;
                max-height:102px;
            }
            .ullDeleteBtn{
                position: absolute;
                font-size: 16px;
                top: -7px;
                right: -7px;
                color: #b1b2b0;
                background-color: #fff;
                border-radius: 50%;
            }
            .maskTips{
                position: absolute;
                bottom: 0;
                width: 102px;
                height: 30px;
                line-height: 30px;
                background-color: rgba(77,77,77,0.75);
                color: #fff;
                text-align: center;
            }
        }
    }
    .loadSpin{
        display: inline-block;
    }
}
