.login{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:100%;
    min-width: 1000px;
    z-index:-10;
    zoom: 1;
    background: url('./img/loginBgi.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center 0;
    .login-form{
        width: 370px;
        padding: 36px;
        background: #fff;
        .login-logo{
            color: #000;
            width: 100%;
            height: 40px;
            font-weight: bold;
            background: url('./img/sun.png') no-repeat;
            background-size: 40px;
            background-position: 10% 15%;
            margin-bottom:10px;
            .login-name{
                line-height: 40px;
                text-align: center;
                padding-left: 40px;
                font-size: 20px;
            }
        }
        .login-flatMsg,.login-title{
            font-size: 14px;
            text-align: center;
        }
        .login-title{
            font-size: 16px;
            margin: 40px 0 24px;
        }
        .login-form-forgot{
            color: #1890FF;
        }
    }
}

.loadingSpin{
    display: flex!important;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}