

.storeManageCont{
  min-height:calc(100vh - 80px);
  .ant-card-body{
    .storeCont{
      min-height:calc(100vh - 180px);
      padding: 20px 30px;
      height: auto;
      overflow: hidden;
      .storeBox{
          height: 200px;
          margin-bottom: 20px;
          .storeCard{
              .storeName{
                margin-left: 20px;
              }
          }
          .waitOnline{
            position: absolute;
            top: 20px;
            right: 20px;
            color: #f00;
          }
      }         
      .cardActionSpan{
        padding: 10px 20px;
      }
    }  
  }        
}
// 复制弹出框
.copyStoreModal{
  .colorBlue{
    color: #3f90f7;
  }
  .checkboxStoreListCont{
      .ant-checkbox-group{
        width: 100%;
        .ant-checkbox-group-item{
        margin: 10px 0;
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
        .ant-checkbox + span{
          padding: 0;
        }
      }
    }
  }

}

// 立即上线弹出框
.onlineStoreModal{
  .ant-modal-body{
    padding-top:40px; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    .onlineBodyTitle{
      margin-top: 20px;
      color: #000;
      font-size: 18px;
      font-weight: bold;
    }
  }
}